import { SET_SCREAMS, LIKE_SCREAM, UNLIKE_SCREAM, LOADING_DATA, DELETE_SCREAM, POST_SCREAM, SET_SCREAM, SUBMIT_COMMENT } from '../types'

const initialState = {
    screams: [],
    scream: {},
    loading: false,

};

export default function(state = initialState, action) {
    switch(action.type) {
        case LOADING_DATA:
            return {
                ...state,
                loading: true
            };
        case SET_SCREAMS:
            return {
                ...state,
                screams: action.payload,
                loading: false
            }
        case SET_SCREAM:
            return {
                ...state,
                scream: action.payload
            }
        case LIKE_SCREAM:
        case UNLIKE_SCREAM:
            let index = state.screams.findIndex((scream) => scream.screamId === action.payload.screamId);
            state.screams[index] = action.payload;
            // 다른 곳에서도 코멘트 카운트를 업데이트하기위함.
            if(state.scream.screamId === action.payload.screamId) {
                state.scream = action.payload
            }
            return {
                ...state,

            }
        case DELETE_SCREAM: 
            //이부분 초기화 때문에 index2를 사용함
            let index2 = state.screams.findIndex(scream => scream.screamId === action.payload);
            state.screams.splice(index2, 1);
            return {
                ...state
            }
        case POST_SCREAM:
            return {
                ...state,
                screams: [
                    action.payload,
                    ...state.screams
                ]
            }
        case SUBMIT_COMMENT:
            return {
                ...state,
                scream: {
                    ...state.scream,
                    comments: [action.payload, ...state.scream.comments]
                }
            }
        default: 
        return state;

    }
}